import React from 'react';
import { TextField, Button, Card, CardContent, Box, Typography, IconButton, List, ListItem, ListItemText, ListItemAvatar, Avatar } from '@mui/material';
import { Link, redirect } from 'react-router';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { AppContext } from '../../contexts';
import { supabase } from '../../supabase';

const CreatePost = () => {
    const { fetchPosts, user } = React.useContext(AppContext);
    const [files, setFiles] = React.useState<FileList | null>(null);
    const [title, setTitle] = React.useState<string>("");
    const [description, setDescription] = React.useState<string>("");

    async function uploadImage(file: File | null) {
        console.log('file', file)
        if (!file || !user) return alert("");

        const filePath = `${user.id}/${Date.now()}${file.name.split(".").pop()}`;
        const { error } = await supabase.storage.from("images").upload(filePath, file);

        if (error) console.error("", error);
        else {
            alert("");
            fetchPosts(); // 
        }
    }

    async function createPost() {
        if (!user) return alert("");

        const image_urls = [];
        if (files) {
            for (let i = 0; i < files.length; i++) {
                const file = files[i];
                const filePath = `${user.id}/${Date.now()}${file.name.split(".").pop()}`;
                const { data, error } = await supabase.storage.from("images").upload(filePath, file);

                if (error) {
                    console.error("", error);
                    return;
                }
                const url = supabase.storage.from("images").getPublicUrl(data.path).data.publicUrl;
                image_urls.push(url);
            }
        }

        const { error } = await supabase
            .from('posts')
            .insert([
                {
                    title,
                    description,
                    image_urls,
                    name: user.user_metadata.full_name,
                    email: user.user_metadata.email,
                    user_avatar_url: user.user_metadata.avatar_url
                }
            ]);

        if (error) {
            console.error("", error);
            alert("");
        } else {
            alert("");
            redirect('/');
        }
    }

    if (!user) {
        redirect('/');
    }

    return (
        <Box
            sx={{
                width: '100%',
                maxWidth: 500,
                margin: '0 auto',
                padding: 2,
                border: '1px solid #ddd',
                borderRadius: 2,
                boxShadow: 3,
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center'
            }}
        >
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', marginBottom: 2 }}>
                <IconButton component={Link} to="/" sx={{ marginBottom: 2 }}>
                    <ArrowBackIcon />
                </IconButton>

                {(!files || files.length === 0)
                    ?
                    <Card sx={{ marginBottom: 2 }}>
                        <CardContent>
                            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <Typography variant="body1" sx={{ marginBottom: 1 }}>
                                    
                                </Typography>
                                <IconButton component="label" sx={{ marginBottom: 2 }}>
                                    <CloudUploadIcon fontSize="large" />
                                    <input type="file" multiple hidden onChange={(event) => { setFiles(event.target.files) }} />
                                </IconButton>
                            </Box>
                        </CardContent>
                    </Card>
                    :
                    <Card sx={{ marginBottom: 2 }}>
                        <CardContent>
                            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <Typography variant="body1" sx={{ marginBottom: 1 }}>
                                     {files.length} 
                                </Typography>
                                <List>
                                    {Array.from(files).map((file, index) => (
                                        <ListItem key={index}>
                                            <ListItemAvatar>
                                                <Avatar src={URL.createObjectURL(file)} variant="square" />
                                            </ListItemAvatar>
                                            <ListItemText primary={files[index].name} />
                                        </ListItem>
                                    ))}
                                </List>
                            </Box>
                        </CardContent>
                    </Card>
                }

                {/* Title Input */}
                <TextField
                    label="Title"
                    fullWidth
                    variant="outlined"
                    margin="normal"
                    sx={{ marginBottom: 2 }}
                    value={title}
                    onChange={(event) => setTitle(event.target.value)}
                />

                {/* Description Input */}
                <TextField
                    label="Description"
                    fullWidth
                    variant="outlined"
                    margin="normal"
                    multiline
                    rows={4}
                    value={description}
                    onChange={(event) => setDescription(event.target.value)}
                />

                {/* Post Button */}
                <Button
                    component="label"
                    role={undefined}
                    variant="contained"
                    tabIndex={-1}
                    startIcon={<CloudUploadIcon />}
                    fullWidth
                    sx={{
                        backgroundColor: "black",
                        color: "white",
                        fontSize: "16px",
                        padding: "12px",
                        borderRadius: "8px",
                        marginBottom: "16px",
                        "&:hover": { backgroundColor: "#333" },
                    }}
                    onClick={createPost}
                >
                    Upload Kitty Card / 
                </Button>
            </Box>
        </Box>
    );
};

export default CreatePost;
